}

const handleSubmit = async () => {
  alert('Ï†úÏ∂ú Í∏∞Îä•ÏùÄ Î∞±ÏóîÎìú ÏúÑÏπòÍ∞Ä Ï†ïÌï¥ÏßÄÎ©¥ Ïó∞Í≤∞Îê† ÏòàÏ†ïÏûÖÎãàÎã§!')
