export async function uploadSnapshot({ timestamp, transcript, screenshot_base64 }) {
  try {
    const response = await axios.post(`${STT_API_BASE}/snapshots`, {
      timestamp,
      transcript,
      screenshot_base64,
    },{
      withCredentials: true  // ‚úÖ Ïó¨Í∏∞Ïóê
    });
    console.log("‚úÖ Ïä§ÎÉÖÏÉ∑ ÏóÖÎ°úÎìú ÏÑ±Í≥µ:", response.data);
    return response.data;
  } catch (error) {
    console.error("‚ùå Ïä§ÎÉÖÏÉ∑ ÏóÖÎ°úÎìú Ïã§Ìå®:", error);
    throw error;
  }
}

