<template>
  <div class="container mt-5">
    <h2 class="text-center">‚ùì ÏßàÎ¨∏ & ÎãµÎ≥Ä ÌôïÏù∏ÌïòÍ∏∞</h2>

    <!-- ÌÉ≠ -->
    <ul class="nav nav-tabs mt-4" style="justify-content: flex-start;">
      <li class="nav-item">
        <a
          class="nav-link"
          :class="{ active: activeTab === 'summary', 'text-primary': true }"
          @click="loadSummary"
        >
          ÏöîÏïΩ Î≥¥Í∏∞
        </a>
      </li>
      <li class="nav-item">
        <a
          class="nav-link"
          :class="{ active: activeTab === 'fullchat', 'text-primary': true }"
          @click="loadFullChat"
        >
          Ï†ÑÏ≤¥ ÎåÄÌôî Î≥¥Í∏∞
        </a>
      </li>
      <li class="nav-item">
        <a
          class="nav-link"
          :class="{ active: activeTab === 'resources', 'text-primary': true }"
          @click="activeTab = 'resources'"
        >
          ÏûêÎ£å Î≥¥Í∏∞
        </a>
      </li>
    </ul>

    <!-- ÌÉ≠ ÎÇ¥Ïö© -->
    <div class="tab-content mt-3 border p-4 rounded bg-white shadow-sm">
      <!-- ‚úÖ ÏöîÏïΩ ÌÉ≠ -->
      <div v-if="activeTab === 'summary'">
        <h5>üìã ÏßàÎ¨∏ ÏöîÏïΩ</h5>

        <div v-if="summaryLoading" class="d-flex align-items-center">
          <strong role="status">Î∂àÎü¨Ïò§Îäî Ï§ë...</strong>
          <div class="spinner-border ms-auto" aria-hidden="true"></div>
        </div>

        <div v-else>
          <h6>üìù ÏöîÏïΩ ÎÇ¥Ïö©</h6>
          <p>{{ summary.summary_for_professor }}</p>

          <h6 class="mt-4">üí° ÏûêÏ£º Î¨ªÎäî ÏßàÎ¨∏</h6>
          <ul>
            <li v-for="(q, index) in summary.most_common_questions" :key="index">
              {{ index + 1 }}. {{ q }}
            </li>
          </ul>
        </div>
      </div>

      <!-- ‚úÖ Ï†ÑÏ≤¥ ÎåÄÌôî ÌÉ≠ -->
      <div v-if="activeTab === 'fullchat'">
        <h5>üí¨ Ï†ÑÏ≤¥ ÎåÄÌôî ÎÇ¥Ïö©</h5>

        <div v-if="chatLoading" class="d-flex align-items-center">
          <strong role="status">Î∂àÎü¨Ïò§Îäî Ï§ë...</strong>
          <div class="spinner-border ms-auto" aria-hidden="true"></div>
        </div>

        <ul v-else class="list-group">
          <li
            v-for="(msg, index) in fullChat"
            :key="index"
            class="list-group-item"
          >
            <p><strong>üßë ÏßàÎ¨∏:</strong> {{ msg.question }}</p>
            <p><strong>ü§ñ ÎãµÎ≥Ä:</strong> {{ msg.answer }}</p>
            <p class="text-muted small">{{ formatDate(msg.created_at) }}</p>
          </li>
        </ul>

        <div v-if="fullChat.length === 0 && !chatLoading" class="text-muted mt-3">
          üì≠ ÏïÑÏßÅ ÎåÄÌôî Í∏∞Î°ùÏù¥ ÏóÜÏäµÎãàÎã§.
        </div>
      </div>

      <!-- ÏûêÎ£å Î≥¥Í∏∞ ÌÉ≠ -->
      <div v-if="activeTab === 'resources'">
        <h5>üìÇ ÏûêÎ£å Î≥¥Í∏∞</h5>
        <p>ÏûêÎ£å Í∏∞Îä•ÏùÄ Ï∂îÌõÑ Ï∂îÍ∞ÄÎê† ÏòàÏ†ïÏûÖÎãàÎã§.</p>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue'
import axios from 'axios'

const activeTab = ref('summary')

const summary = ref({
  most_common_questions: [],
  summary_for_professor: '',
})
const fullChat = ref([])

const summaryLoading = ref(false)
const chatLoading = ref(false)

function formatDate(dateStr) {
  const d = new Date(dateStr)
  return d.toLocaleString('ko-KR', {
    year: 'numeric',
    month: 'short',
    day: 'numeric',
    hour: '2-digit',
    minute: '2-digit'
  })
}

const loadSummary = async () => {
  activeTab.value = 'summary'
  summaryLoading.value = true

  try {
    const token = localStorage.getItem('access_token')
    if (!token) throw new Error('‚ùå ÌÜ†ÌÅ∞ ÏóÜÏùå')

    const response = await axios.get('https://project2025-backend.onrender.com/chat_history/summary', {
      headers: {
        Authorization: `Bearer ${token}`
      }
    })

    summary.value = response.data
  } catch (error) {
    console.error('‚ùå ÏöîÏïΩ Îç∞Ïù¥ÌÑ∞ Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®:', error)
    summary.value = {
      summary_for_professor: '‚ö†Ô∏è ÏöîÏïΩ Îç∞Ïù¥ÌÑ∞Î•º Î∂àÎü¨Ïò¨ Ïàò ÏóÜÏäµÎãàÎã§.',
      most_common_questions: []
    }
  } finally {
    summaryLoading.value = false
  }
}

const loadFullChat = async () => {
  activeTab.value = 'fullchat'
  chatLoading.value = true

  try {
    const token = localStorage.getItem('access_token')
    if (!token) throw new Error('‚ùå ÌÜ†ÌÅ∞ ÏóÜÏùå')

    const response = await axios.get('https://project2025-backend.onrender.com/chat_history/all', {
      headers: {
        Authorization: `Bearer ${token}`
      }
    })

    fullChat.value = response.data || []
  } catch (error) {
    console.error('‚ùå Ï†ÑÏ≤¥ ÎåÄÌôî Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®:', error)
    fullChat.value = []
  } finally {
    chatLoading.value = false
  }
}

// ‚úÖ ÏµúÏ¥àÏóî ÏöîÏïΩ Î®ºÏ†Ä Î°úÎî©
loadSummary()
</script>

