<template>
    <div class="container mt-5">
      <h2 class="mb-4">üìö ÎÇ¥ ÎåÄÌôî Í∏∞Î°ù</h2>
  
      <div v-if="loading" class="d-flex align-items-center">
        <strong role="status">Î∂àÎü¨Ïò§Îäî Ï§ë...</strong>
        <div class="spinner-border ms-auto" aria-hidden="true"></div>
      </div>
  
      <ul v-else class="list-group">
        <li
          v-for="(msg, index) in chatHistory"
          :key="index"
          class="list-group-item"
        >
          <p><strong>üßë ÏßàÎ¨∏:</strong> {{ msg.question }}</p>
          <p><strong>ü§ñ ÎãµÎ≥Ä:</strong> {{ msg.answer }}</p>
          <p class="text-muted small">{{ formatDate(msg.created_at) }}</p>
        </li>
      </ul>
  
      <div v-if="chatHistory.length === 0 && !loading" class="text-muted mt-3">
        üì≠ ÏïÑÏßÅ ÎåÄÌôî Í∏∞Î°ùÏù¥ ÏóÜÏäµÎãàÎã§.
      </div>
    </div>
  </template>
  
  <script setup>
  import { ref, onMounted } from 'vue'
  import axios from 'axios'
  
  const chatHistory = ref([])
  const loading = ref(true)
  
  function formatDate(dateStr) {
    const d = new Date(dateStr)
    return d.toLocaleString('ko-KR', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    })
  }
  
  onMounted(async () => {
    loading.value = true
  
    try {
      const token = localStorage.getItem('access_token')
      if (!token) throw new Error('ÌÜ†ÌÅ∞ ÏóÜÏùå')
  
      const response = await axios.get('https://project2025-backend.onrender.com/chat_history/me', {
        headers: {
          Authorization: `Bearer ${token}`
        },
        withCredentials: true
      })
  
      chatHistory.value = response.data || []
    } catch (error) {
      console.error('‚ùå ÎåÄÌôî Í∏∞Î°ù Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®:', error)
      chatHistory.value = []
    } finally {
      loading.value = false
    }
  })
  </script>
  