import axios from 'axios'

const BASE_URL = 'https://project2025-backend.onrender.com' // ‚úÖ Î∞òÎìúÏãú HTTPS Ï£ºÏÜå!

// üñºÔ∏è Ïä§ÎÉÖÏÉ∑ ÏóÖÎ°úÎìú
export async function uploadSnapshot({ timestamp, transcript, screenshot_base64 }) {
  try {
    const response = await axios.post(`${BASE_URL}/snapshots`, {
      timestamp,
      transcript,
      screenshot_base64,
    },{
      withCredentials: true  // ‚úÖ Ïó¨Í∏∞Ïóê
    })
    console.log("‚úÖ Ïä§ÎÉÖÏÉ∑ ÏóÖÎ°úÎìú ÏÑ±Í≥µ:", response.data)
    return response.data
  } catch (error) {
    console.error("‚ùå Ïä§ÎÉÖÏÉ∑ ÏóÖÎ°úÎìú Ïã§Ìå®:", error.response?.data || error.message || error)
    throw error
  }
}
