<template>
    <div class="p-6">
      <h1 class="text-3xl font-bold mb-6">Ïã§ÏãúÍ∞Ñ ÏßàÎ¨∏ ÏãúÏó∞</h1>
      
      <div class="mb-4">
        <button @click="startVAD" class="bg-blue-500 hover:bg-blue-600 text-white font-bold py-2 px-4 rounded mr-2">
          ÏùåÏÑ± Í∞êÏßÄ ÏãúÏûë
        </button>
        <button @click="stopVAD" class="bg-red-500 hover:bg-red-600 text-white font-bold py-2 px-4 rounded">
          ÏùåÏÑ± Í∞êÏßÄ Ï§ëÏßÄ
        </button>
      </div>
  
      <div class="mt-4">
        <p>ÌòÑÏû¨ ÏÉÅÌÉú: <strong>{{ vadStatus }}</strong></p>
      </div>
    </div>
  </template>
  
  <script>
  import { WebRTCVoiceActivityDetector } from '@ricky0123/vad-web';
  
  export default {
    name: 'ProfessorRealtimeQuestion',
    data() {
      return {
        vad: null,
        vadStatus: 'Ï†ïÏßÄÎê®', // ÏÉÅÌÉú ÌëúÏãú: Ï†ïÏßÄÎê®, ÏùåÏÑ± Í∞êÏßÄÏ§ë, Î¨¥Ïùå ÏÉÅÌÉú
      };
    },
    methods: {
      async startVAD() {
        if (this.vad) {
          this.vad.stop();
        }
  
        const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
  
        this.vad = new WebRTCVoiceActivityDetector(stream, (event) => {
          if (event.eventType === 'voice') {
            this.vadStatus = 'ÏùåÏÑ± Í∞êÏßÄÎê® üé§';
            console.log('ÏùåÏÑ± Í∞êÏßÄÎê®!');
          } else if (event.eventType === 'silence') {
            this.vadStatus = 'Î¨¥Ïùå ÏÉÅÌÉú ü§´';
            console.log('Î¨¥Ïùå ÏÉÅÌÉú!');
          }
        }, {
          positiveSpeechThreshold: 0.9,
          negativeSpeechThreshold: 0.8,
          minSpeechFrames: 5,
          preSpeechPadFrames: 10
        });
  
        this.vad.start();
        this.vadStatus = 'ÏùåÏÑ± Í∞êÏßÄ ÏãúÏûëÎê® üéôÔ∏è';
      },
      stopVAD() {
        if (this.vad) {
          this.vad.stop();
          this.vad = null;
        }
        this.vadStatus = 'Ï†ïÏßÄÎê®';
      }
    }
  };
  </script>
  
  <style scoped>
  button {
    transition: background-color 0.3s;
  }
  </style>
  