<template>
  <div class="lecture-container text-center mt-5">
    <h2>üé§ ÏàòÏóÖ ÎÖπÌôî & ÏùåÏÑ± Ïù∏Ïãù</h2>
    <p class="text-muted">ÎÖπÏùå Ï§ë ÌÇ§ÏõåÎìúÍ∞Ä Í∞êÏßÄÎêòÎ©¥ ÏûêÎèôÏúºÎ°ú ÌôîÎ©¥ Ï∫°Ï≤òÏôÄ Ìï®Íªò Î∞±ÏóîÎìúÏóê Ï†ÑÏÜ°Îê©ÎãàÎã§.</p>

    <div class="btn-group mt-4">
      <button class="btn btn-danger m-2" @click="toggleScreenRecording">
        {{ isScreenRecording ? "üõë ÌôîÎ©¥ ÎÖπÌôî Ï§ëÏßÄ" : "üìπ ÌôîÎ©¥ ÎÖπÌôî ÏãúÏûë" }}
      </button>

      <button class="btn btn-primary m-2" @click="toggleAudioRecording">
        {{ isAudioRecording ? "üõë ÏùåÏÑ± Ïù∏Ïãù Ï¢ÖÎ£å" : "üéôÔ∏è ÏùåÏÑ± Ïù∏Ïãù ÏãúÏûë" }}
      </button>
    </div>
  </div>
</template>

<script>
import { uploadSnapshot } from "@/api/snapshotService"

export default {
  name: "ProfessorLesson",
  data() {
    return {
      isAudioRecording: false,
      isScreenRecording: false,
      audioRecorder: null,
      audioStream: null,
      audioChunks: [],
      recognition: null,
      displayStream: null,
      triggerKeywords: ["Î≥¥Î©¥", "Î≥¥Í≤å ÎêòÎ©¥", "Ïù¥ Î∂ÄÎ∂Ñ", "Ïù¥Í±∏ Î≥¥Î©¥", "ÏΩîÎìú", "ÌôîÎ©¥", "Ïó¨Í∏∞", "Ïù¥Ï™Ω"],
    }
  },
  methods: {
    async toggleAudioRecording() {
      if (!this.isAudioRecording) {
        try {
          this.audioStream = await navigator.mediaDevices.getUserMedia({ audio: true })
          this.audioRecorder = new MediaRecorder(this.audioStream)
          this.audioChunks = []

          this.audioRecorder.ondataavailable = (e) => {
            if (e.data.size > 0) this.audioChunks.push(e.data)
          }

          this.audioRecorder.onstop = () => {
            const blob = new Blob(this.audioChunks, { type: "audio/webm" })
            const url = URL.createObjectURL(blob)
            const a = document.createElement("a")
            a.href = url
            a.download = "audio-recording.webm"
            a.click()

            if (this.audioStream) {
              this.audioStream.getTracks().forEach((track) => track.stop())
              this.audioStream = null
            }

            if (this.displayStream) {
              this.displayStream.getTracks().forEach((track) => track.stop())
              this.displayStream = null
            }
          }

          this.displayStream = await navigator.mediaDevices.getDisplayMedia({ video: true })
          this.audioRecorder.start()
          this.isAudioRecording = true
          this.startRecognition()
        } catch (err) {
          console.error("‚ùå Ïò§ÎîîÏò§ ÎÖπÏùå ÎòêÎäî ÌôîÎ©¥ Ï∫°Ï≤ò Í∂åÌïú Ïã§Ìå®:", err)
        }
      } else {
        this.audioRecorder.stop()
        this.isAudioRecording = false
        this.stopRecognition()
      }
    },

    toggleScreenRecording() {
      // ÏÑ†ÌÉùÏ†Å Í∏∞Îä• (ÏÉùÎûµ)
    },

    startRecognition() {
      const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition
      if (!SpeechRecognition) {
        alert("Î∏åÎùºÏö∞Ï†ÄÍ∞Ä ÏùåÏÑ± Ïù∏ÏãùÏùÑ ÏßÄÏõêÌïòÏßÄ ÏïäÏïÑÏöî üôÅ")
        return
      }

      this.recognition = new SpeechRecognition()
      this.recognition.lang = "ko-KR"
      this.recognition.continuous = true
      this.recognition.interimResults = false

      this.recognition.onresult = (event) => {
        const transcript = event.results[event.results.length - 1][0].transcript
        console.log("üé§ ÏùåÏÑ± Ïù∏Ïãù Í≤∞Í≥º:", transcript)

        const hit = this.triggerKeywords.some((kw) => transcript.includes(kw)) ||
                    /Î≥¥.*Î©¥|ÏΩîÎìú|ÌôîÎ©¥|Ïó¨Í∏∞|Ïù¥ Î∂ÄÎ∂Ñ|Ïù¥Ï™Ω/.test(transcript)

        if (hit) {
          this.takeScreenshotAndUpload(transcript)
        }
      }

      this.recognition.onerror = (event) => {
        console.error("ÏùåÏÑ± Ïù∏Ïãù ÏóêÎü¨:", event.error)
      }

      this.recognition.start()
    },

    stopRecognition() {
      if (this.recognition) {
        this.recognition.stop()
        this.recognition = null
      }
    },

    async takeScreenshotAndUpload(transcript) {
      try {
        if (!("ImageCapture" in window)) {
          console.error("‚ùå ImageCapture APIÎ•º ÏßÄÏõêÌïòÏßÄ ÏïäÎäî Î∏åÎùºÏö∞Ï†ÄÏûÖÎãàÎã§.")
          return
        }

        if (!this.displayStream) return

        const track = this.displayStream.getVideoTracks()[0]
        const imageCapture = new ImageCapture(track)
        const bitmap = await imageCapture.grabFrame()

        const canvas = document.createElement("canvas")
        canvas.width = bitmap.width
        canvas.height = bitmap.height
        const ctx = canvas.getContext("2d")
        ctx.drawImage(bitmap, 0, 0)
        const imageBase64 = canvas.toDataURL("image/png")

        const now = new Date()
        const timestamp = `${now.getFullYear()}-${(now.getMonth() + 1)
          .toString()
          .padStart(2, "0")}-${now.getDate().toString().padStart(2, "0")} ${now
          .getHours()
          .toString()
          .padStart(2, "0")}:${now.getMinutes().toString().padStart(2, "0")}:${now
          .getSeconds()
          .toString()
          .padStart(2, "0")}`

        await uploadSnapshot({
          timestamp,
          transcript,
          screenshot_base64: imageBase64,
        })

        console.log("‚úÖ Î∞±ÏóîÎìúÏóê Ïä§ÎÉÖÏÉ∑ ÏóÖÎ°úÎìú ÏôÑÎ£å")
      } catch (err) {
        console.error("‚ùå Ïä§ÌÅ¨Î¶∞ÏÉ∑ ÏóÖÎ°úÎìú Ïã§Ìå®:", err.response?.data || err.message || err)
      }
    }
  }
}
</script>

<style scoped>
.lecture-container {
  max-width: 900px;
  margin: auto;
  padding: 30px;
}
</style>
