<template>
  <div class="container mt-5" style="max-width: 700px;">
    <h2 class="mb-4">üìù ÏÉà Í≥ºÏ†ú Í≥µÏßÄ ÏûëÏÑ±</h2>

    <form @submit.prevent="handleSubmit">
      <div class="mb-3">
        <label class="form-label">Í≥ºÏ†ú Ï†úÎ™©</label>
        <input v-model="title" type="text" class="form-control" required />
      </div>

      <div class="mb-3">
        <label class="form-label">Í≥ºÏ†ú ÏÑ§Î™Ö</label>
        <textarea v-model="description" class="form-control" rows="4" required></textarea>
      </div>

      <div class="mb-3">
        <label class="form-label">ÎßàÍ∞êÏùº</label>
        <input v-model="due" type="date" class="form-control" required />
      </div>

      <button type="submit" class="btn btn-primary">Í≥µÏßÄ Îì±Î°ù</button>
    </form>

    <div v-if="statusMessage" class="alert alert-info mt-3">
      {{ statusMessage }}
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue'
import axios from 'axios'
import { useRouter } from 'vue-router'

const title = ref('')
const description = ref('')
const due = ref('')
const statusMessage = ref('')
const router = useRouter()

const handleSubmit = async () => {
  if (!title.value || !description.value || !due.value) return

  const token = localStorage.getItem('access_token') // üîê ÌÜ†ÌÅ∞ Í∞ÄÏ†∏Ïò§Í∏∞

  try {
    await axios.post('https://project2025-backend.onrender.com/assignments/create', {
      title: title.value,
      description: description.value,
      due: due.value,
    }, {
      headers: {
        Authorization: `Bearer ${token}`, // üîê Ìó§ÎçîÏóê ÌÜ†ÌÅ∞ Ìè¨Ìï®
      }
    })

    statusMessage.value = '‚úÖ Í≥µÏßÄÍ∞Ä ÏÑ±Í≥µÏ†ÅÏúºÎ°ú Îì±Î°ùÎêòÏóàÏäµÎãàÎã§!'
    setTimeout(() => {
      router.push('/professor/assignments')
    }, 1000)
  } catch (err) {
    console.error('Í≥µÏßÄ Îì±Î°ù Ïã§Ìå®:', err)
    statusMessage.value = '‚ùå Í≥µÏßÄ Îì±Î°ù Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.'
  }
}
</script>
